.inputs {
  display: flex;
  flex-direction: column;
  align-items: flex-end;

  &__title {
    text-align: center;
  }

  &__wrapper {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;

    &__hidden {
      display: none;
    }
  }
}
